}
@font-face {
    font-display: swap;
    font-family: "Pacifico";
    font-style: normal;
    font-weight: 400;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format("woff2"),
        url(${___CSS_LOADER_URL_REPLACEMENT_3___}) format("truetype");
}
@font-face {
    font-display: swap;
    font-family: "Noto Serif Georgian";
    font-style: normal;
    font-weight: 400;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_4___})
            format("woff2"),
        url(${___CSS_LOADER_URL_REPLACEMENT_5___})
            format("truetype");
}

@font-face {
    font-display: swap;
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_6___}) format("woff2"),
        url(${___CSS_LOADER_URL_REPLACEMENT_7___}) format("truetype");
}

:root {
    --red-orange: #ff6e2b;
    --orange: #ff8d4a;
}

*,
*::after,
*::before {
    box-sizing: border-box;
}

body {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
    margin: 0;
    padding: 0;
    font-family: Roboto, system-ui, -apple-system, BlinkMacSystemFont,
        "Segoe UI", Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
        sans-serif;
    line-height: 1.5;
}
p {
    font-family: Roboto, sans-serif;
    text-wrap: pretty;
}
h1,
h2,
h3 {
    text-wrap: balance;
}
h3 {
    font-family: Noto Serif Georgian, sans-serif;
}
h2 {
    margin-bottom: 0;
}
.icon {
    height: 3ch;
}

header {
    display: grid;
    justify-content: center;
    justify-items: center;
    font-family: Galdeano, sans-serif;
    background-color: rgb(255, 0, 0);
    padding: 1rem;
    position: sticky;
    top: 0;
    box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.36);
    color: white;

    & h1 {
        margin-top: 0;
        font-size: 3rem;
        display: flex;
        align-items: end;
    }
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

button {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
}

button:hover {
    color: var(--orange);
}

::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-track {
    background: white;
}
::-webkit-scrollbar-thumb {
    background: var(--orange);
}
::-webkit-scrollbar-thumb:hover {
    background: var(--red-orange);
}

header ul {
    font-size: 2rem;
    display: flex;
    gap: 2rem;
}

#content {
    margin-left: auto;
    margin-right: auto; /* center content */
    background-color: white;
    padding: 3rem 4rem;
    width: calc(4rem + 70ch);
}

.main section {
    display: flex;
    flex-direction: column;
    align-items: start;
    & li:not(.menu-list > li) {
        width: 33ch;
        display: flex;
        justify-content: space-between;
    }
    & button {
        font-family: Pacifico, sans-serif;
        background-color: red;
        border-radius: 1rem;
        padding: 1rem 2rem;
        color: white;
    }
    & button:hover {
        color: var(--orange);
    }
}

.about section {
    display: flex;
    flex-direction: column;
    align-items: start;
    & li {
        width: 33ch;
        display: flex;
        justify-content: space-between;
    }
    & button {
        font-family: Pacifico, system-ui, -apple-system, BlinkMacSystemFont,
            "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
            "Helvetica Neue", sans-serif;
        background-color: red;
        border-radius: 1rem;
        padding: 1rem 2rem;
        color: white;
    }

    .location-slider,
    img {
        align-self: center;
        width: 133%;
        border: 2px solid white;
    }

    .location-slider > img {
        width: 100%;
    }
    li > .icon {
        width: min-content;
    }
}

.location-slider {
    position: relative;
    display: flex;
    & p {
        margin: 0;
        width: 100%;
        text-align: center;
        padding: 1rem;
        background-color: rgba(0, 0, 0, 0.116);
        backdrop-filter: blur(5px);
        position: absolute;
    }
    .button-left-slider,
    .button-right-slider {
        cursor: pointer;
        border-top: 15px solid transparent;
        border-bottom: 15px solid transparent;
        bottom: 50%;
        position: absolute;
    }
    .button-left-slider {
        margin-left: 1rem;
        border-right: 30px solid var(--orange);
    }

    .button-right-slider {
        right: 0;
        margin-right: 1rem;
        border-left: 30px solid var(--orange);
    }
    .button-right-slider:hover {
        border-left: 30px solid var(--red-orange);
    }
    .button-left-slider:hover {
        border-right: 30px solid var(--red-orange);
    }
}

.menu section {
    margin: 1rem;
}

.menu-list {
    display: flex;
    margin: 1rem 0;
    color: gray;
    gap: 2rem;
}
.menu-items {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    justify-items: center;
    & li {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        padding-bottom: 1rem;
        text-align: center;
        background-color: rgb(246, 246, 246);

        & img {
            max-width: 100%;
        }
        & button {
            font-family: Pacifico, system-ui, -apple-system, BlinkMacSystemFont,
                "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
                "Helvetica Neue", sans-serif;
            background-color: red;
            border-radius: 1rem;
            padding: 1rem 2rem;
            margin-top: auto;
            color: white;
        }
        & button:hover {
            color: var(--orange);
        }
    }
    * {
        margin: 0;
    }
}
